.question__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.single-question {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.single-question__title {
    margin-bottom: 20px;
    font-size: 28px;
    font-weight: 700;
    text-align: center;
}
.single-question__anwers {
    display: flex;
    flex-direction: column;
    width: 90%;
}
.single-question__anwers-item {
    font-size: 20px;
    width: 100%;
    padding: 10px;
    background-color: #ffffff;
    border-radius: 10px;
    box-shadow: 0px 0px 10px 2px #000000;
    color: #2264AB;
    text-align: center;
    cursor: pointer;
    transition: 0.4s;
    &:not(:last-child) {
        margin-bottom: 10px;
    }
    &:hover {
        background-color: #c1d9fd;
    }
}
.correct {
    background-color: rgb(5, 255, 67);
    &:hover {
        background-color: rgb(5, 255, 67);
    }
}
.incorrect {
    background-color: #cd3232;
    &:hover {
        background-color: #cd3232;
    }
}
.question__reset {
    background: #ffffff;
    padding: 5px 10px;
    border: none;
    border-radius: 10px;
    color: #2264AB;
    font-size: 22px;
    transition: 0.4s;
    align-self: flex-start;
    margin-bottom: 20px;
    box-shadow: 0px 0px 10px 2px #000000;
    &:hover {
        background: #d3d9fa;
    }
}
.question__score {
    font-size: 18px;
    font-weight: 700;
    margin-bottom: 15px;
}
.question__pagination {
    margin-top: 20px;
    font-weight: 700;
    font-size: 18px;
}
